.App {
  max-width: 800px;
  background-color: rgb(75, 68, 92);
  margin: auto;
  display: flex;
  flex-direction: column;
}

.chat-room {
  display: flex;
  flex-direction: column;
}

.msgs {
  overflow-y:auto;
  height: 80vh;
}

#signout {
  background-color: rgb(22, 18, 60);
  height: 30%;
  display: flex;
}

#signout-btn {
  margin: auto;
  width: 8rem;
}

.message {
  display: flex;
  align-items: center;
}

.sent {
  flex-direction: row-reverse;
}

.received {
  flex-direction: row;
}

img {
  height: 3rem;
  width: 3rem;
  border-radius: 50%;
  margin: .5rem;
}

.chat-msg {
  background-color: white;
  border-radius: 2rem;
  padding: 1rem;
  max-width: 20rem;
}

#input-field {
  display: flex;
}

#input-field input {
  /* position: fixed;
  bottom: 0; */
  width: 100%;
  height: 3rem;
  font-size: 2rem;
}

#input-field button {
  width: 10%;
  background-color: rgb(44, 36, 114);
}

#input-field button:hover {
  background-color: rgb(22, 18, 60);
}

#input-field button:active {
  background-color: rgb(3, 2, 7);
}

















